<template>
    <div class="List">
        <div class="list__out">
            <input type="hidden" value='index'>
            <input type="checkbox" :checked="checkflag" @change="check('{{lid}}','{{checkflag}}')">
            {{index}} {{detail}}
            <button class="list__right" @click="remove('{{lid}}')" type="button">delete</button>
        </div>
    </div>
</template>
<script>
import axios from 'axios';
import VueAxios from 'vue-axios';
export default {
    name: 'List',
    methods: {
        remove(lid){
            console.log('remove : '+this.lid);
            let id = this.lid;
            let checkflag = this.checkflag;
            let chflag = true
            if(checkflag) {
                chflag = false
            } 
            let payload = {
                action: 'remove',
                index: this.index,
                item : {
                    lid:id
                }
            };
            this.$store.commit('todoStore/updateData', payload)
        },
        check(lid,flag){
            let id = this.lid;
            let checkflag = this.checkflag;
            console.log('check : ' + id);
            let chflag = true
            if(checkflag) {
                chflag = false
            } 
            let payload = {
                action: 'check',
                index: this.index,
                item : {
                    lid: id,
                    checkflag: chflag,
                }
            };
            this.$store.commit('todoStore/updateData', payload)
        }
    },
    props: {
        lid: String,
        memberid: String,
        detail: String,
        checkflag: Boolean,
        index: Number
    },
}
</script>
